// 在真实环境中，如果使用firebase这种第三方auth服务的话，本文件不需要开发者开发

import { User } from "type/user";
import { AuthForm } from "context/auth-context";

const API = process.env.REACT_APP_API_URL;
const localToken = "_auth_token";

export const getToken = () => window.localStorage.getItem(localToken);

export const handleUserResponse = ({ user }: { user: User }) => {
  window.localStorage.setItem(localToken, user.token || "");
  return user;
};

export const login = (data: AuthForm) => {
  return fetch(`${API}/login`, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify(data),
  }).then(async (response) => {
    if (response.ok) {
      return handleUserResponse(await response.json());
    } else {
      return Promise.reject(await response.json());
    }
  });
};

export const register = (data: AuthForm) => {
  return fetch(`${API}/register`, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify(data),
  }).then(async (response) => {
    if (response.ok) {
      return handleUserResponse(await response.json());
    } else {
      return Promise.reject(await response.json());
    }
  });
};

export const logout = async () => window.localStorage.removeItem(localToken);
