export const useMount = (callback: () => void) => {
  useEffect(() => callback(), []);
};

export const useDebounce = <V>(value: V, delay?: number) => {
  const [debValue, setDebValue] = useState(value);

  useEffect(() => {
    // 每次在value变化后，设定一个定时器
    const timeout = setTimeout(() => setDebValue(value), delay);
    // 每次在上一个useEffect处理完以后在运行
    return () => clearTimeout(timeout);
  }, [value, delay]);
  return debValue;
};
// 动态变换标题
export const useDocumentTitle = (
  title: string,
  keepOnUnmount: boolean = true
) => {
  //页面加载时:oldTitle === 旧 title 'React App"
  //加载后:oldTitle ===新title
  //useRef保存最初的值，组件生命周期发生变化，他的值不会变化
  const oldTitle = useRef(document.title).current;
  useEffect(() => {
    document.title = title;
  }, [title]);

  useEffect(() => {
    return () => {
      if (!keepOnUnmount) {
        // 当不指定依赖时，读到的就是旧的title--闭包陷阱
        document.title = oldTitle;
      }
    };
  }, [title, oldTitle, keepOnUnmount]);
};

export const resetRoutes = () =>
  (window.location.href = window.location.origin);

/**
 * 存在问题，如果组件在异步请求的过程中突然被卸载，系统会报错，所以设置下面的hook
 * @returns 返回组件的挂载状态，如果还没挂载或者已经卸载，返回false，反之，返回true
 */
export const useMountRef = () => {
  const mountedRef = useRef(false);

  useEffect(() => {
    // 组件加载的时候设置为true
    mountedRef.current = true;
    // 组件卸载设置为false
    return () => {
      mountedRef.current = false;
    };
  }, [mountedRef]);
  return mountedRef;
};
