import { Dropdown, Menu, Modal, Table, TableProps } from "antd";
import { ButtonNoPadding } from "components/lib";
import { Pin } from "components/pin";
import dayjs from "dayjs";
import React from "react";
import { Link } from "react-router-dom";
import { useDeleteProject, useEditProject } from "utils/project";
import { Project } from "../../type/project";
import { User } from "../../type/user";
import { useProjectModal, useProjectQueryKey } from "./util";
interface ListProps extends TableProps<Project> {
  users: User[];
  refresh?: () => void;
}

export const List = ({ users, ...props }: ListProps) => {
  const { mutate } = useEditProject(useProjectQueryKey());

  // 柯里化 id：在函数生成之前就存在，可以先传入，
  // pin是在函数生成的时候才出现，所以后传入
  const handlePinProject = (id: number) => (pin: boolean) =>
    mutate({ id, pin });

  return (
    <Table
      pagination={false}
      {...props}
      rowKey={"id"}
      columns={[
        {
          title: <Pin checked={true} />,
          render: (project) => {
            return (
              <Pin
                checked={project.pin}
                onCheckChanged={handlePinProject(project.id)}
              />
            );
          },
        },
        {
          title: "名称",
          sorter: (a, b) => a.name.localeCompare(b.name),
          render(value, project) {
            return <Link to={`${project.id}`}>{project.name}</Link>;
          },
        },
        {
          title: "部门",
          dataIndex: "organization",
        },
        {
          title: "创建时间",
          dataIndex: "created",
          render(project) {
            return (
              <span>
                {project.created
                  ? "无"
                  : dayjs(project.created).format("YYYY-MM-DD")}
              </span>
            );
          },
        },
        {
          title: "负责人",
          dataIndex: "personId",
          render(value, project) {
            return (
              <span>
                {users.find((users) => users.id === project.personId)?.name ||
                  "未知"}
              </span>
            );
          },
        },
        {
          render(value, project) {
            return <More project={project} />;
          },
        },
      ]}
    />
  );
};

const More = ({ project }: { project: Project }) => {
  const { starEdit } = useProjectModal();
  const { mutate: deleteProject } = useDeleteProject(useProjectQueryKey());
  const handleEditProject = (id: number) => () => starEdit(id);
  const confirmDeleteProject = (id: number) => {
    Modal.confirm({
      title: "确定删除这个项目吗？",
      content: "点击确定删除",
      okText: "确定",
      onOk() {
        deleteProject({ id });
      },
    });
  };
  return (
    <Dropdown
      overlay={
        <Menu>
          <Menu.Item onClick={handleEditProject(project.id)} key={"edit"}>
            编辑
          </Menu.Item>
          <Menu.Item
            onClick={() => confirmDeleteProject(project.id)}
            key={"delete"}
          >
            删除
          </Menu.Item>
        </Menu>
      }
    >
      <ButtonNoPadding type={"link"}>. . .</ButtonNoPadding>
    </Dropdown>
  );
};
