import { Button, Card, Divider, Typography } from "antd";
import React, { useState } from "react";
// import { Helmet } from "react-helmet";
import { LoginScreen } from "./login";
import { RegisterScreen } from "./register";
import styled from "@emotion/styled";
import logo from "assets/logo.svg";
import left from "assets/left.svg";
import right from "assets/right.svg";
import { ErrorBox } from "screens/project-list";

export const UnauthenticatedApp = () => {
  const [isRegistered, setIsRegistered] = useState(false);
  const [error, setError] = useState<Error | null>(null);
  // useDocumentTitle('登录注册')
  return (
    <Container>
      <Background />
      <Header />
      <ShadowCard>
        <Title>{isRegistered ? "注册" : "登录"}</Title>
        {/* {error ? (
          <Typography.Text type="danger">{error.message}</Typography.Text>
        ) : null} */}
        <ErrorBox error={error} />

        {isRegistered ? (
          <RegisterScreen onError={setError} />
        ) : (
          <LoginScreen onError={setError} />
        )}
        <Divider />
        <LoginA type={"link"} onClick={() => setIsRegistered(!isRegistered)}>
          {isRegistered ? "已经有账号了？去登录" : "没有账号？去注册"}
        </LoginA>
      </ShadowCard>
    </Container>
  );
};

const Title = styled.h2`
  margin-bottom: 2.4rem;
  color: rgb(94, 108, 132);
`;

const Background = styled.div`
  position: absolute;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: left bottom, right bottom;
  background-size: calc(
    (((100vm-4rem) / 2)-3.2rem),
    (((100vm-4rem) / 2)-3.2rem)
  );
  background-image: url(${left}), url(${right});
`;

const Header = styled.header`
  background: url(${logo}) no-repeat center;
  padding: 5rem;
  background-size: 8rem;
  width: 100%;
`;

const ShadowCard = styled(Card)`
  width: 40rem;
  min-height: 56rem;
  padding: 3.2rem 4rem;
  border-radius: 0.3rem;
  box-sizing: border-box;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 10px;
  text-align: center;
`;

const LoginA = styled(Button)`
  font-size: 0.1rem;
`;

const Container = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
`;
