import * as auth from "auth-provider";
import { useAuth } from "context/auth-context";
import qs from "qs";
import { useCallback } from "react";
const API = process.env.REACT_APP_API_URL;
// fetch 的初始化类型
interface ConfigApi extends RequestInit {
  data?: object;
  token?: string;
}

export const HttpRequest = (
  api: string,
  //   设置一个默认值为{} 则参数变为可选值
  { data, token, headers, ...customConfig }: ConfigApi = {}
) => {
  const config = {
    method: "GET",
    headers: {
      Authorization: token ? `Bearer ${token}` : "",
      "Content-Type": data ? "application/json" : "",
    },
    ...customConfig,
  };
  if (config.method.toUpperCase() === "GET") {
    // 将对象序列化成URL的形式，以&进行拼接
    api += `?${qs.stringify(data)}`;
  } else {
    config.body = JSON.stringify(data || {});
  }

  //   axios 和fetch的表现 不一样，axios可以直接在返回状态不为2xx是抛出异常
  return window.fetch(`${API}/${api}`, config).then(async (response) => {
    if (response.status === 401) {
      auth.logout();
      window.location.reload();
      return Promise.reject({ message: "请重新登录" });
    }
    const data = await response.json();
    if (response.ok) {
      return data;
    } else {
      // 在fetch中必须要手动抛出异常
      return Promise.reject(data);
    }
  });
};

export const useHttp = () => {
  const { user } = useAuth();
  // 设定数组内的类型为 http 的类型
  // 在元组前面加... 可以保证里面的参数可选
  return useCallback(
    (...[api, config]: Parameters<typeof HttpRequest>) =>
      HttpRequest(api, { ...config, token: user?.token }),
    [user?.token]
  );
};
