import { useMemo } from "react";
import { URLSearchParamsInit, useSearchParams } from "react-router-dom";
import { cleanObj } from "utils";
/**
 *
 * @param key 返回页面url中，指定键的参数值
 * http://localhost:3000/projects?name=骑手
 * @returns
 */
// 从url中读取值
export const useUrlQueryParam = <K extends string>(keys: K[]) => {
  const [searchParams] = useSearchParams();
  const setSearchParam = useSetUrlSearchParam();
  return [
    //   解决无限循环
    useMemo(
      () =>
        keys.reduce((pre, key) => {
          // 如果上面不 extends string
          // 计算属性名的类型必须为 "string"、"number"、"symbol" 或 "any"。
          return { ...pre, [key]: searchParams.get(key) || "" };
        }, {} as { [key in K]: string }),
      [searchParams]
    ),
    (params: Partial<{ [key in K]: unknown }>) => {
      return setSearchParam(params);
    },
  ] as const;
};

// 设置url的值
export const useSetUrlSearchParam = () => {
  const [searchParams, setSearchParams] = useSearchParams();

  return (params: { [key in string]: unknown }) => {
    const o = cleanObj({
      // Object.fromEntries作用：将数组[value,key]转换成{value:key}的形式
      ...Object.fromEntries(searchParams),
      // 用新的对象覆盖旧的
      ...params,
    }) as URLSearchParamsInit;
    return setSearchParams(o);
  };
};
