import { FullPageErrorMes, FullPageLoading } from "components/lib";
import React, { ReactNode } from "react";
import { useQueryClient } from "react-query";
import { User } from "type/user";
import { useMount } from "utils";
import { HttpRequest } from "utils/http";
import { useAsync } from "utils/use-async";
import * as auth from "../auth-provider";

export interface AuthForm {
  username: string;
  password: string;
}

// 解决value的类型是undefined
const AuthContext = React.createContext<
  | {
      user: User | null;
      login: (form: AuthForm) => Promise<void>;
      logout: () => Promise<void>;
      register: (form: AuthForm) => Promise<void>;
    }
  | undefined
>(undefined);

AuthContext.displayName = "AuthContext";
// 在页面刷新时更新token的值
export const defaultUser = async () => {
  let user = null;
  const token = auth.getToken();
  if (token) {
    const data = await HttpRequest("me", { token });
    user = data.user;
  }
  return user;
};

export const AuthProvider = ({ children }: { children: ReactNode }) => {
  const {
    run,
    data: user,
    setData: setUser,
    isIdle,
    isLoading,
    isError,
    error,
  } = useAsync<User | null>();

  const queryClient = useQueryClient();

  const login = (form: AuthForm) => auth.login(form).then(setUser);
  const register = (form: AuthForm) => auth.register(form).then(setUser);
  const logout = () =>
    auth.logout().then(() => {
      setUser(null);
      queryClient.clear();
    });

  // 页面加载时，更新user
  useMount(() => {
    defaultUser().then(setUser);
  });
  if (isIdle || isLoading) {
    return <FullPageLoading />;
  }
  if (isError) {
    return <FullPageErrorMes error={error} />;
  }

  return (
    <AuthContext.Provider
      children={children}
      value={{ user, login, logout, register }}
    />
  );
};

export const useAuth = () => {
  // 使用context
  const context = React.useContext(AuthContext);
  if (!context) {
    throw new Error("useAuth 必须在AuthProvider中使用");
  }
  return context;
};
